.button-bar {
  display: flex;
  flex-wrap: wrap;
  column-gap: 1ch;
  row-gap: .5em;
}

.button-bar .button-group {
  display: flex;
  flex-wrap: wrap;
  column-gap: .75ch;
  row-gap: .5em;
}

.button-bar button {
  block-size: 2.5em;
  opacity: 0.85;
}

.button-bar button:not([disabled]):hover {
  opacity: 1;
}

.button-bar button {
  padding-inline: 1.5ch;
  font-family: "Exo";
}

.button-bar button:not([disabled]) {
  cursor: pointer;
}

@media (max-width: 80ch) {
  .button-bar button {
    block-size: 2.25em;
  }

  .button-bar button {
    padding-inline: 1ch;
  }
}
